.viewer{
    width: 100%;
    height: 100vh;
    background: #000;
    border-bottom: 2px solid #07b9ff;
    position: relative;
}
.image-viewer{
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.viewer-image{
    width: 100%;
    height: 100%;
    position: relative;
}


.viewer-image .spinner-container{
    width: 100%;
    height: 100% !important;
    position: absolute;
    top: 0;
    left: 0;
}

.image-viewer img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}


.toolbar{
    width: 100%;
    height: 64px;
    background: #000;
    padding: 2px 5px;
    z-index: 1000;
    position: relative;
}

.toolbar-content{
    width: 100%;
    height: 100%;
    max-width: 360px;
    display: flex;
    margin: 0 auto;
    gap: 4px;
}

.toolbar-content button{
    width: 20%;
    text-align: center;
    height: 100%;
    color: #07b9ff;
    display: flex;
    align-items: center;
    justify-content: center;
}

.toolbar-content button.disable{
    opacity: 0.5;
    pointer-events: none;

}


.pdflink{
    padding: 8px 10px;
}



.toolbar-content button div{
    width: 100%;
    display: flex;
    flex-direction: column;
}


.toolbar-content button div i{
    width: 100%;
    height: 32px;
    line-height: 32px;
    text-align: center;
    font-size: 1.3rem;
}
.toolbar-content button div i.icon-invert_colors_on{
    font-size: 1.6rem !important;
}



.toolbar-content button div span{
    font-size: .65rem;
}




.image-information{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 10px;
    z-index: 1000;
    pointer-events: none;
}

.image-information .patient-info{
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 50%;
    padding: 5px;
}

.image-information  span{
    color: #07b9ff;
    font-size: .825rem;
    display: block;
}


.image-information .study-info{
    position: absolute;
    top: 0;
    right: 0;
    width: 50%;
    height: 50%;
    padding: 5px;
    text-align: right;
}

.image-information .image-info{
    position: absolute;
    bottom: 30px;
    left: 0;
    width: 50%;
    padding: 5px;
    text-align: left;
}


.react-transform-wrapper{
    width: 100% !important;
    height: 100% !important;
}

.react-transform-component{
    width: 100% !important;
    height: 100% !important;
}


.show-info {
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    background: rgba(0, 0, 0 , 0.8);
    z-index: 100000;
}

.show-info .container{
    width: 100%;
    height: 100%;
    background: #fff;
    max-width: 640px;
    margin: 0 auto;

}


.show-info .header{
    width: 100%;
    height: 50px;
    position: sticky;
    top: 0;
    background: #fff;
    padding: 0 5px;
    box-shadow: 0 0 3px rgba(0, 0, 0 , 0.26);
}
.show-info .header-container{
    width: 100%;
    height: 50px;
    position: relative;
    display: flex;
}

.show-info .header-container button{
    width: 50px;
    height: 50px;
    font-size: 1.1rem;
    text-align: center;
}

.show-info .header-container span{
    flex-grow: 1;
    line-height: 50px;
}


.show-info-item{
    line-height: 50px;
}

.show-info-item .key{
    padding: 0 5px;
    height:25px;
    line-height: 25px;
    background: #444;
    font-size: .7rem;
    width: 100%;
    font-weight: bold;
    color: #fdfdfd;

}

.show-info-item .value{
    padding: 0 5px;
    height: 100%;
    background: #fafafa;
    font-size: .75rem;
    width: 100%;
    width: 100%;
    flex-grow: 1;
}






.show-info-body{
    width: 100%;
    height: 100vh;
    height: calc(100vh - 50px);
    overflow: auto;
}
.display-none{
    display: none;
}

.tools-info{
    position: absolute;
    bottom: 30px;
    right: 0;
    padding: 5px;
}

.scrollable-view{
    width: 100%;
    height: 26px;
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 3px 10px;
}



.scrollable-view input{
    width: 100%;
    --trackBg:#163239;
    --thumbBg:#07aed8;
    --thumbBorderRadius:6px;
    --thumbWidth:36px;
    --thumbHeight:12px;
}


input[type=range] {
    -webkit-appearance: none; /* Hides the slider so that custom slider can be made */
    width: 100%; /* Specific width is required for Firefox. */
    background: transparent; /* Otherwise white in Chrome */
  }
  
  input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
  }
  
  input[type=range]:focus {
    outline: none; /* Removes the blue border. You should probably do some kind of focus styling for accessibility reasons though. */
  }
  
  input[type=range]::-ms-track {
    width: 100%;
    cursor: pointer;
  
    /* Hides the slider so custom styles can be added */
    background: transparent; 
    border-color: transparent;
    color: transparent;
  }
  


  input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: var(--thumbHeight);
    width: var(--thumbWidth);
    border-radius: var(--thumbBorderRadius);
    background: var(--thumbBg);
    cursor: pointer;
  }
  
  /* All the same stuff for Firefox */
  input[type=range]::-moz-range-thumb {
    height: var(--thumbHeight);
    width: var(--thumbWidth);
    border-radius: var(--thumbBorderRadius);
    background: var(--thumbBg);
    cursor: pointer;
  }
  
  /* All the same stuff for IE */
  input[type=range]::-ms-thumb {
    height: var(--thumbHeight);
    width: var(--thumbWidth);
    border-radius: var(--thumbBorderRadius);
    background: var(--thumbBg);
    cursor: pointer;
  }



  input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    height:  var(--thumbHeight);
    cursor: pointer;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    background: var(--trackBg);
    border-radius: 1.3px;
    border-radius: var(--thumbBorderRadius);
  }
  
  input[type=range]:focus::-webkit-slider-runnable-track {
    background: var(--trackBg);
  }
  
  input[type=range]::-moz-range-track {
    width: 100%;
    height: var(--thumbHeight);
    cursor: pointer;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    background: var(--trackBg);
    border-radius: var(--thumbBorderRadius);
  }
  
  input[type=range]::-ms-track {
    width: 100%;
    height:  var(--thumbHeight);
    cursor: pointer;
    background: transparent;
    border-color: transparent;
    border-width: 16px 0;
    border-radius: var(--thumbBorderRadius);
    color: transparent;
  }
  input[type=range]::-ms-fill-lower {
    background: var(--trackBg);
    border-radius: var(--thumbBorderRadius);
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  }
  input[type=range]:focus::-ms-fill-lower {
    background: var(--trackBg);
  }
  input[type=range]::-ms-fill-upper {
    background: var(--trackBg);
    border-radius: var(--thumbBorderRadius);
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  }
  input[type=range]:focus::-ms-fill-upper {
    background: var(--trackBg);
  }

