body{
  background: #f1f1f1;
}

.footer{
  text-align: center;
}

.footer a{
  color: #333;
  text-decoration: none;
}

.full {
  width: 100%;
  height: 100vh;
  background: #333;
}

.flex{
  display: flex;
}

.flex-center{
  display: flex;
  align-items: center;
  justify-content: center;
}

@keyframes loader{
  0% {
    opacity: 1;
  }
  50% {
    opacity: .2;
  }
  100% {
    opacity: 1;
  }
}

.logo h1{
  color: #f1f1f1;
  text-align: center;
  animation: loader 6s infinite;
}

button{
  background: none;
  border: none;
}

.mx-640{
  max-width: 640px;
  margin: 0 auto;
}

.relative{
  position: relative;
}

a{
  text-decoration: none;
}

@media screen and (max-width:998px) {
  .full {
    width: 100%;
    height: 100vh;
    height: calc(100vh - 54px);
    background: #333;
  }
  
}
