.header{
    width: 100%;
    height: 54px;
    background: #000;
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3);
    position: sticky;
    top: 0;
    z-index: 10000;
    border-bottom: 2px solid #2c92d6;
}

.header-content{
    display: flex;
    padding: 2px 10px;
    margin: 0 auto;
}

.header-content .header-items{
    flex-grow: 1;
    gap: 5px;
    line-height: 50px;
    height: 50px;
}


.header-content h1{
    line-height: 50px;
    font-size: 1.3rem;

}

.header-content button {
    color: #2c92d6;
}


.header-content button {
    color: #2c92d6;
}

.header-content button span{
    line-height: 50px;
    height: 50px;
    display: inline-block;
    vertical-align: top;
    padding-left: 8px;
}



.header-content .header-items button .icon-arrow-left{
    font-size: 1rem;
    line-height: 50px;
    color: #2c92d6;
    padding-left: 0;
}

.header-content button.icon-log-out{
    line-height: 50px;
    font-size: 1.3rem;
}

.error-page{
    background: #fdfdfd;
    padding:  0 30px;
    
}

.error-page p{
    direction: rtl;
    text-align: right;
}
.directory{
    background: #fff;
}

.directory-header{
    width: 100%;
    height: 40px;
    background: #2c92d6;
    line-height: 40px;
    padding: 0 10px;
    color: #f1f1f1;
    font-weight: bold;
    font-size: .875rem;
}

.list{
    min-height: 100vh;
    min-height: calc(100vh - 154px);
    background: #fff;
}

.directory .items{
    padding: 12px 0;
}

.directory .item{
    width: 100%;
    border-bottom: 1px solid #ddd;
}

.directory .item:last-child{
    width: 100%;
    border-bottom: none;
}

.directory .item .content{
    width: 100%;
    display: flex;
    gap: 10px;
    padding: 5px;
}

.directory .item .content:hover{
    cursor: pointer;
}

.directory .item .image-holder{
    width: 140px;
    height: 100px;
    border-radius: 8px;
    background: #000;
    border: 1px solid #6fadff;
    overflow: hidden;
}

.directory .item .image-holder img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}



.directory .title > span{
    width: 100%;
    padding: 10px 0;
    font-size: .875rem;
    font-weight: bold;
    color: #444;
    display: block;
}

.directory .title > span{
    width: 100%;
    padding: 10px 0;
    font-size: .875rem;
    font-weight: bold;
    color: #444;
    display: block;
}

.directory .seriesInfo *{
    font-size: .8rem;
    color: #666;
}





.directory .item .image-holder .spinner-container{
    height: 100% !important;
    top: 0;
    left: 0;
    position: absolute;
    min-height: 0 !important;
}

.support{
    background: #f1f1f1;
    font-size: .8rem;
    padding: 20px;
}

.list-footer{
    width: 100%;
    height: 40px;
    line-height: 40px;
    background: #000;
    color: #2c92d6;
    text-align: center;
    border-top: 2px solid #2c92d6;
    font-size: .8rem;
}

.list-footer a{
    position: relative;
    color:#2c92d6 ;
    font-weight: bold;
}

.list-footer a::after{
    content:  " ";
    width: 100%;
    position: absolute;
    bottom: -2px;
    height: 1px;
    left: 0;
    background: #2c92d6;
}