
.spinner-container{
  width: 100%;
  height: 100vh;
  position: relative;
  text-align: center;
}
#root > .spinner-container{
  height: 100vh;
}

.main > .spinner-container{
  height: calc(100vh - 120px) !important;
}
.main_content > .spinner-container{
  height: calc(100vh - 120px) !important;
}
.spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -30px 0 0 -30px;
  display: inline-block;
  height: 60px;
  width: 60px;
  -webkit-animation: rotation .6s infinite linear;
  -moz-animation: rotation .6s infinite linear;
  -o-animation: rotation .6s infinite linear;
  animation: rotation .6s infinite linear;
  border: 6px solid rgba(0, 174, 249, .15);
  border-top: 6px solid rgba(0, 174, 249, .8);
  border-radius: 100%;
}

.items .spinner-container{
  width: 100%;
  height: 100vh;
  position: relative;
  min-height: 160px;
}
.items .spinner-container .spinner{
  width: 32px;
  height: 32px;
  opacity: .5;
  margin: -16px 0 0 -16px;
}
@-webkit-keyframes rotation {
  from {-webkit-transform: rotate(0deg);}
  to   {-webkit-transform: rotate(359deg);}
}

@-moz-keyframes rotation {
  from {-moz-transform: rotate(0deg);}
  to   {-moz-transform: rotate(359deg);}
}

@-ms-keyframes rotation {
  from {-moz-transform: rotate(0deg);}
  to   {-moz-transform: rotate(359deg);}
}

@-o-keyframes rotation {
  from {-o-transform: rotate(0deg);}
  to   {-o-transform: rotate(359deg);}
}

@keyframes rotation {
  from {transform: rotate(0deg);}
  to   {transform: rotate(359deg);}
}

@media screen and (max-width: 980px){
  .spinner{
      width: 40px;
      height: 40px;
      margin: -20px 0 0 -20px;
  }
}

.spinner_text{
  text-align: center;
  color: #20A5D6;
  font-size: 1rem;
  line-height: 2;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,60px);
}

.bg{
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background: #f1f1f1;
}
.logintofile{
  width: 100%;
  max-width: 600px;
  padding: 0 10px;
  margin: 0 auto;
  position: relative;
  top: 50%;
  transform: translate(0,-50%);

  border-radius: 15px;
  padding: 70px 100px;
  background: #111;
  background-color: rgba(0, 0, 0, .3);
}
.logintofile h2{
  color: #fdfdfd;
  font-size: 1.3rem;
}
.logintofile p{
  font-size: 1rem;
  color: #f1e0df;
}
.logintofile .inputHolder{
  width: 100%;
  max-width: 100%;
  height: 50px;
  padding: 0;
  position: relative;
}
.logintofile .inputHolder input{
  width: 100%;
  outline: none;
  height: 100%;
  padding: 0;
  border: 2px solid #20A5D6;
  border: none;
  background: #fdfdfd;
  color: #20A5D6;
  color: #222;
  padding: 0 12px;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 5px;
}

.logintofile button{
  color: #fdfdfd;
  margin: 30px 0 0 0;
  background: #20A5D6;
  border-radius: 5px;
  padding: 8px 12px;
  border: none;
  outline: none;
  font-size: 1rem;
  cursor: pointer;
  margin-right: 12px;
}
.logintofile button:hover{
  color: #fdfdfd;
  background: #1a82a8;
}


.myLoadingStatus{
  line-height: 50px;
  height: 50px;
  padding: 0 20px;
  border-radius: 5px;
  background: #20a5d6;
  color: #fdfdfd;
  position: fixed;
  top: 130px;
  left: 40px;
  z-index: 1000;
}

@media screen and (max-width:600px) {
  .logintofile{
    border-radius: 0;
    padding: 50px 10px;
  }
}


.lds-ring {
  display: block;
  position: relative;
  width: 66px;
  height: 66px;
  margin: 24px auto;
  margin: 10vh auto;
}
.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 56px;
  height: 56px;
  margin: 5px;
  border: 5px solid rgb(214, 23, 221);
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: rgb(95, 7, 88) transparent transparent transparent;
  
}
.lds-ring.inline {
    display: block;
    position: relative;
    margin: 0 auto !important;
}
.lds-ring.inline div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0;
  border: 3px solid #456787;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #456787 transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}
@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
